div.form {
  background-image: url("../pictures/lucas-favre-JnoNcfFwrNA-unsplash.jpg");
  background-repeat: no-repeat;
  background-color: rgba(0, 0, 0, 0.7);
  background-blend-mode: multiply;
  color: white;
  box-shadow: 0 0 15px 20px rgb(137, 137, 137);
  width: 100%;
  height: 50rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
div.form .border {
  border: 5px solid rgb(255, 255, 255);
  border-radius: 2rem;
  padding: 20px;
}
div.form h1 {
  font-size: 3rem;
}
div.form div.username {
  display: flex;
  align-items: center;
}
div.form div.username label.userlabel {
  font-size: 1.5rem;
  padding-right: 20px;
  font-weight: 700;
}
div.form div.username input.usernameinput {
  width: 300px;
  height: 30px;
}
div.form div.password {
  display: flex;
  align-items: center;
}
div.form div.password label.passwordlabel {
  font-size: 1.5rem;
  padding-right: 20px;
  margin-top: 30px;
  font-weight: 700;
}
div.form div.password input.passwordinput {
  width: 300px;
  height: 30px;
  margin-top: 30px;
}
div.form button.button {
  font-size: 2rem;
  margin-top: 30px;
  width: 380px;
  height: 50px;
  cursor: pointer;
  border-radius: 1rem;
  font-weight: 700;
}
div.form button.button:hover {
  font-size: 2rem;
  background-color: gray;
  color: white;
  border: 1px solid rgb(244, 242, 242);
}

@media screen and (max-width: 500px) {
  div.form .border {
    border: 5px solid rgb(255, 255, 255);
    border-radius: 2rem;
    padding: 20px;
  }
  div.form h1 {
    font-size: 2rem;
  }
  div.form div.username label.userlabel {
    font-size: 1rem;
    padding-right: 20px;
    font-weight: 700;
  }
  div.form div.username input.usernameinput {
    width: 220px;
    height: 30px;
  }
  div.form div.password label.passwordlabel {
    font-size: 1rem;
    padding-right: 20px;
    margin-top: 30px;
    font-weight: 700;
  }
  div.form div.password input.passwordinput {
    width: 220px;
    height: 30px;
    margin-top: 30px;
  }
  div.form button.button {
    font-size: 1.75rem;
    margin-top: 30px;
    width: 275px;
    height: 50px;
  }
  div.form button.button:hover {
    font-size: 1.75rem;
  }
}/*# sourceMappingURL=Login.css.map */